.card{
  border-radius: 10px;
  --bg-opacity: 1;
  background-color: #F9FBFF;
  background-color: rgba(249, 251, 255, var(--bg-opacity));
  overflow: hidden;
  margin-bottom: 4rem;
  width:100%;
  box-shadow:0 8px 30px rgba(47,85,212,0.12);
  position:relative;
  transition:all 0.25s ease-in-out;
  bottom:0
}

.card .card-image{
  max-height:170px;
  display:inline-block;
  overflow:hidden;
  width:100%
}

.card h3,.card img{
  transition:all 0.25s ease-in-out
}

.card img{
  width:100%;
  max-height:180px;
  object-fit:cover
}

.card:hover{
  bottom:0px;
  box-shadow:0 8px 30px rgba(47,85,212,0.25);
  color:white;
  background-color:#2F55D4
}

.card:hover h3{
  color:white
}

.card:hover img{
  transform:scale(1.05)
}

.card:hover .more::after{
  background-color:white
}

.card .more{
  margin-top: 0.5rem;
  padding-left: 1rem;
  padding-right: 1rem;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem
}

.card .more::after{
  margin-left:10px;
  width:9px;
  height:15px;
  content:'';
  mask:url("../images/arrow.svg");
  mask-size:cover;
  display:inline-block;
  position:relative;
  top:2px;
  color:inherit;
  background-color:#102A3D;
  transition:all 0.25s ease-in-out
}

#more{
  margin-top: 2rem;
  margin-bottom: 1rem;
  --border-opacity: 1;
  border-color: #2F55D4;
  border-color: rgba(47, 85, 212, var(--border-opacity));
  --text-opacity: 1;
  color: #2F55D4;
  color: rgba(47, 85, 212, var(--text-opacity));
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
  background-color:transparent
}

#more:hover{
  color:white;
  background-color:#2F55D4
}

