@import 'roots';
@import '../../node_modules/tailwindcss/base';
@import '../../node_modules/tailwindcss/components';
@import '../../node_modules/tailwindcss/utilities';

// General
$min_width: 320px;
$max_width: 1400px;
$min_font: 10px;
$max_font: 16px;

html {
  @include fluid-type($min_width, $max_width, $min_font, $max_font);
  scroll-behavior: smooth;
}

/* utility */
html.sr .load-hidden {
    visibility: hidden;
}

::selection {
    color: $lighter;
    background-color: $theme;
}

h1, h2, h3, h4, h5 {
    font-weight: bold;
}

button {
    @apply px-12 py-4 font-bold bg-blue text-white rounded;
    border: 2px solid;
    transition: $transition;
    &:hover {
        @apply border-blue;
    }
}

nav li {
    margin-left: 38px;
    &::after {
        content: '';
        display: block;
        position: relative;
        top: 0.5rem;
        width: 0;
        opacity: 0;
        border: 2px solid $dark;
        transition: 0.15s width ease-in-out;
    }
    &:hover {
        &::after {
            opacity: 1;
            width: 75%;
        }
    }
}

/* underline - hover.css */
.underline {
    display: inline-block;
    vertical-align: middle;
    transform: perspective(1px) translateZ(0);
    position: relative;
  }
  .underline:before {
    content: "";
    position: absolute;
    z-index: -1;
    left: 51%;
    right: 51%;
    bottom: 0;
    background: $theme;
    height: 4px;
    transition-property: left, right;
    transition-duration: 0.15s;
    transition-timing-function: ease-in-out;
  }
  .underline:hover:before, .underline:focus:before, .underline:active:before {
    left: -1rem;
    right: -1rem;
  }

// Hero
.headshot {
    height: 442px;
    img {
        width: 100%;
        max-width: 400px;
        object-fit: cover;
    }
}

// Overview
#overview_container {
    background-image: url('/images/shapes.svg');
    background-size: auto 100%;
    background-repeat: repeat-y;
}

.feature { 
    width: 100%;
    object-fit: cover;
}

section {
    p {
        @apply text-s20;
        font-weight: 300;
    }
    h1 {
        @apply pb-8 text-s46;
        display: inline-block;
    }
}

.tool {
    @apply flex justify-center;
    img {
        transition: $transition;
        width: 100%;
        height: auto;
        object-fit: contain;
    }
}

// Get in touch
.touch {
    p {
        a {
            padding: 2px 8px;
            margin-right: -8px;
            border-bottom: 2px solid $theme;
            background: transparent;
            transition: $transition;
            &:hover {
                margin-right: 0;
                color: white;
                background-color: $theme;
            }
        }
    }
}

// Blog
.content {
    @apply p-4;
    h3 {
        @apply text-dark mb-4 text-s24;
    }
    p {
        @apply font-light;
        font-size: 1rem;
    }
}

.card {
    @apply rounded bg-lighter overflow-hidden mb-16;
    width: 100%;
    box-shadow: $shadow;
    position: relative;
    transition: $transition;
    bottom: 0;
    .card-image {
        max-height: 170px;
        display: inline-block;
        overflow: hidden;
        width: 100%;
    }
    h3, img {
        transition: $transition;
    }
    img {
        width: 100%;
        max-height: 180px;
        object-fit: cover;
    }
    // When you hover things go whoosh!
    &:hover {
        bottom: 0px;
        box-shadow: $shadow-lg;
        color: white;
        background-color: $theme;
        h3 {
            color: white;
        }
        img {
            transform: scale(1.05);
        }
        .more::after {
            background-color: white;
        }
    }
    .more {
        @apply mt-2 px-4 py-2;
        &::after {
            margin-left: 10px;
            width: 9px;
            height: 15px;
            content: '';
            mask: url('../images/arrow.svg');
            mask-size: cover;
            display: inline-block;
            position: relative;
            top: 2px;
            color: inherit;
            background-color: $dark;
            transition: $transition;
        }
    }
}

// More on Medium Button
#more {
    @apply mt-8 mb-4 border-blue text-blue py-3;
    background-color: transparent;
    &:hover {
        color: white;
        background-color: $theme;
    }
}

// Footer
footer {
    h1 {
        a {
            padding: 2px 1rem;
            margin-left: -1rem;
            border-bottom: 4px solid $theme;
            background: transparent;
            transition: $transition;
            &:hover {
                margin-left: 0;
                @apply text-white;
                background-color: $theme;
            }
        }
    }
    .copyright {
        opacity: 0.75;
        font-weight: 300;
        transition: $transition;
        a:hover {
            &::before, &::after {
                content: 'üç∞';
            }
            &::before {
                margin-right: 5px;
            }
            &::after {
                margin-left: 5px;
            }
        }
    }
}
.socials {
    ul {
        @apply my-10;
        li:not(:first-of-type) {
            @apply pl-12;
        }
    }
}


@media (max-width: 1400px) {
    .container {
        width: 100% !important;
        max-width: none;
    }
    #overview_container {
        background-size: 100% auto;
    }
    #uxfix {
        &::after {
            margin-left: unset !important;
        }
    }
    .card:not(:last-of-type) {
        margin-right: 1rem;
    }
}

@media (max-width: 1024px) {
    #overview_container {
        background-image: none;
    }
    footer {
        h1 {
            a {
                margin-left: 0;
            }
        }
    }
}

@media (min-width: 1024px) {
    .card {
        margin-bottom: 0;
        width: 340px;
    }
}